.input {
    line-height: 28px;
    border: 2px solid transparent;
    border-bottom-color: #112324;
    padding: .2rem 0;
    outline: none;
    background-color: transparent;
    color: #0d0c22;
    transition: .3s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.input:focus, input:hover {
    outline: none;
    padding: .2rem 1rem;
    border-radius: 1rem;
    border-color: #020707;
}

.input::placeholder {
    color: #031617;
}

.input:focus::placeholder {
    opacity: 0;
    transition: opacity .3s;
}